.swiperSection {
  position: relative;
}

.slide {
  display: flex;
  align-items: center;
  height: calc(100vh - 83px);
}

.bg {
  position: absolute;
  inset: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.info img {
  width: 437px;
}

.info p {
  font-size: 38px;
  color: var(--main-text-color);
  font-weight: 800;
  margin-bottom: 10px;
}
.info p span {
  font-size: 60px;
  color: var(--main-color);
  border-bottom: 5px solid var(--main-color);
}

.medicine {
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading {
  font-size: 40px;
  font-weight: 700;
  color: #1dd2c8;
  margin-bottom: 10px;
}

.headingImg {
  width: 150px;
}

.medicine .tovars {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}

.news {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  padding: 30px 0 20px;
}
@media (max-width: 1079px) {
  .container {
    padding: 0 15px;
  }
  .info {
    width: 100%;
    left: 52%;
    bottom: -10%;
  }
}

/* mobile  */
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
  .slide {
    height: 60vh;
  }
  div[class^='swiper-button-'] {
    display: none;
  }
  .swiper-pagination {
    transform: translateY(-20px);
  }
  .info {
    width: 100%;
    left: 55%;
  }
  .info p {
    font-size: 26px;
  }
  .info p span {
    font-size: 40px;
  }
  .info img {
    width: 200px;
  }
  .medicine .tovars {
    grid-template-columns: 1fr;
  }
  .news {
    grid-template-columns: 1fr;
  }
}
